<template>
  <div>
    <h2>修改密码</h2>
    <p>
      <label>原密码</label>
      <el-input v-model="oldPwd"
                clearable>
      </el-input>
    </p>
    <p>
      <label>新密码</label>
      <el-input v-model="Npwdval1"
                show-password
                clearable>
      </el-input>
    </p>
    <p>
      <label>确认新密码</label>
      <el-input v-model="Npwdval2"
                show-password
                clearable>
      </el-input>
    </p>
    <p>
      <label></label>
      <el-button type="primary"
                 @click="addpwdr()">确定</el-button>
      <el-button type="primary"
                 plain
                 @click="reset()">重置</el-button>
    </p>
  </div>
</template>

<script>
import { checkoldpwd } from '@/API/apis'
import { editpwd } from '@/API/apis'

export default {
  data () {
    return {
      oldPwd: '',
      id: '',
      Npwdval1: '',
      Npwdval2: '',
    }
  },
  created () {
    this.id = localStorage.getItem('id')
  },
  methods: {
    // 修改密码
    addpwdr () {
      //检查旧密码是否正确
      checkoldpwd(this.oldPwd, this.id).then(res => {
        if (res.data.code !== 0) {
          this.$message.error(res.data.msg);
          return
        } else {
          editpwd(this.Npwdval1, this.id).then(res => {
            if (res.data.code == 0) {
              this.$message({
                message: res.data.msg,
                type: 'success'
              });
              this.$router.push("/")
            }
          })
        }
      })
    },
    reset () {
      this.oldPwd = "",
        this.Npwdval1 = "",
        this.Npwdval2 = ""
    }
  }
}
</script>

<style lang="less" scoped>
p {
  padding-bottom: 25px;
  .el-input,
  .el-select {
    width: 20%;
  }
  label {
    display: inline-block;
    width: 100px;
    margin-right: 20px;
    color: #808080;
    text-align: right;
  }
}
</style>